@import url(https://cdn.jsdelivr.net/npm/animate.css@3.5.2/animate.min.css);
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

* {
  font-family: Arial, Helvetica, sans-serif;
}
.container {
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  padding: 15px;
  min-height: 400px;
}
.options {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.options.bottom {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-flow: row dense;
    grid-gap: 15px;
}
.options.bottom button {
      border: 0px;
      padding: 10px;
      border-radius: 5px;
      background: #35495e;
      color: whitesmoke;
}
.options.bottom button.active {
        background: #42b883;
        border: 0px;
}
.options span {
    -ms-flex-item-align: center;
        align-self: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.todo-input {
  padding: 15px;
  margin: 15px 0px;
  font-size: 20px;
  color: #42b883;
  width: 100%;
}
.todo-input:focus {
    outline: 0;
}
.edit-todo {
  grid-row: 2/3;
  grid-column: 1/-1;
  margin: 15px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  border: 0px;
}
.input-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  padding-bottom: 15px;
  margin: 15px 15px  30px 15px;
  border-bottom: 1px solid #e0e0e0;
}

.todo {
  margin: 15px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  display: grid;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
  grid-template-columns: 1fr 10fr 1fr;
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 10px;
}
.todo .remove-todo {
    grid-column: 3/4;
    justify-self: center;
    color: #42b883;
}
.todo .done-todo {
    justify-self: center;
    color: #42b883;
}
.todo .task-info {
    padding-left: 15px;
}
.todo .doneTask {
    text-decoration: line-through;
    opacity: 0.5;
}
